.subHeader{
    padding: 30px 0 0;
    background-color: #F2F2F2;
}
.subHeader_section{
    border-bottom: 1px solid rgba(217, 216, 214, 1);
    padding-bottom: 20px;
}
.subHeader_buttons{
    gap: 10px;
}
.button{
    position: relative;
}
.clear{
    position: absolute;
    right: 0;
    height: 100%;
    background-color: transparent;
    border: none;
    padding: 0 14px;
    cursor: pointer;
}
.button, .subHeader_buttons a{
    cursor: pointer;
    padding: 9px;
    transition: .2s ease-in-out;
}
.button:hover, .subHeader_buttons a:hover{
    background-color: #61C4BF;
}
.profile_links a:hover{
    transition: .2s ease-in-out;
    color: #61C4BF
}
.button svg{
    width: 18px;
    height: 18px;
}
.button svg path{
    fill: white;
}
.product_count{
    padding-left: 10px;
}
.profile_links{
    gap: 10px;
    margin-left: 40px;
}
.subHeader-info{
    gap: 16px;
}
.subHeader-info span{
    font-size: 14px;
    font-weight: 500;
}
.subHeader-inner{
    gap: 15px;
}
.subHeader-socials{
    gap: 8px;
}
.subHeader-socials span{
    cursor: pointer;
}
.subHeader-langs{
    gap: 4px;
}
.subHeader-langs span{
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    padding: 2px 8px;
    cursor: pointer;
    border-radius: 16px;
    background: #F3F4F6;
}
.searchInput {
  background-color: white !important;
  width: 200px;
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 32px;
  outline: none;
  transition: width 0.3s ease-in-out;
}
.searchInput:focus {
  width: 250px;
  border-color: #000;
}
@media (max-width: 960px) {
    .profile_links, .product_count{
        display: none !important;
    }
}
@media screen and (max-width: 768px) {
    .subHeader{
        display: none;
    }
}